function updateUserInfo(phone,token) {
   uni.request({
      url:'https://test134.vrapp.chot.cn/api' ,//
      data: {
				operate:"MemberCenter.update",
				token:token,						 	
        phone:phone
      },
      method: 'POST',
      header: {
        'content-type': 'application/json'
      },
       success: (res) => {
				console.log("");
				console.log(res);
      }
    });
      

			
}
module.exports = {
	updateUserInfo: updateUserInfo,
}
